.position-section {
  padding: 24px 0;
  @extend .bottom-border;

  .position-section-content {
    display: inline-block;
    @extend .position-info-font;
    width: 680px;
    padding: 0 16px;
  }
}

.company-job {
  /* 与position-listh中的通用 */
  .info-top {
    font-weight: 400;
    div {
      line-height: 24px;
    }
    .title  {
      color: rgba(0,0,0,0.87);
    }
    .member-position {
      color: rgba(73, 177, 142, 0.87);
    }
    time {
      color: rgba(0,0,0,0.28);
    }
    span {
      margin-right: 16px;
    }
  }

  .info-bottom {
    padding-top: 12px;
    @extend .position-info-font;
    span {
      padding-right: 8px;
    }
  }
}
.company-job,
.company-info{
  p,label,span {
    display: inline-block;
    vertical-align: top;
    @extend .invitation-content-font;
  }

  .company-title {
    label {
      display: inline-block;
      font-size: 14px;
      padding-right: 8px;
    }
    span {
      display: inline-block;
      padding-left: 8px;
    }
    .title-indicator {
      display: inline-block;
      width: 2px;
      height: 14px;
      background-color: #E6E6E6;
    }
  }

  .company-location {
    span {
      padding-left: 8px;
    }
  }

  .company-tag {

    .tag-info-item {
      display: inline-block;

      span {
        padding-left: 8px;
        margin-right: 20px;
      }
    }
  }

  .company-site {

    a {
      font-size: 14px;
      color: $primary-color;
    }
  }

  .info-bottom.company-intro {
    padding-top: 8px;
    display: inline-block;
    width: 100%;

    p {
        line-height: 20px;
    }
  }
}

/* 公司信息页样式 */
.company-edit-container {
  width: 550px;
  display: inline-block;
  text-align: center;
  .content-action {
    display: inline-block;
  }
}

.company-card {
  width: 780px;
  margin: 0 auto;
}
