.position-container {
  background : $primary-content-color;
  min-height: 500px;
  @extend .z-depth-1;
}

.invitation-container {
  height: 100%;
}

.invtation-details-container {
  padding: 16px 24px;
}

.inv-num {
  padding-top: 16px;
}

/* 邀请详细信息页面公司详情 */
.position-section {
  padding: 16px 0;
  @extend .bottom-border;
  /* 与position-listh中的通用 */
  .position-section-content {
    @extend .md-caption;

    display: inline-block;
    width: 650px;
    padding: 0 16px;
  }

  .info-top {
    font-weight: 400;
    .title  {
      color: rgba(0,0,0,0.87);
    }
    .member-position {
      color: rgba(73, 177, 142, 0.87);
    }
    time {
      color: rgba(0,0,0,0.28);
    }
    span {
      margin-right: 16px;
    }
  }

  .info-bottom {
    padding-top: 12px;
    @extend .position-info-font;
    span {
      padding-right: 8px;
    }
  }
}

#add-new-position {
  display: inline-block;
  padding: 0 24px;
  height: 48px;
  line-height: 48px;
  width: 100%;
  @extend .bottom-border;
  color: $primary-color;
  cursor: pointer;
}

#position-modal {
  display: inline-block;
  width: 578px;
  min-width: 578px;
  max-height: 850px;
  text-align: left;
  /* position: absolute; */
  /* margin: 0 auto;
     left: 0;
     right: 0;
 */
  .header-action {
    top: 4px;
  }
  .template {
    margin: 0;
  }

  .modal-footer {
    position: relative;
    .btn-flat {
      margin-right: 16px;
    }
    .footer-action {

      position: absolute;
      right: 0;
      top: 0;
    }
    .footer-extra {
      display: inline-block;

      label,a {
        cursor: pointer;
        @extend .footer-caption-font;
      }
    }
  }
}

.datepicker {
  position: relative;
}

/* 没有任何职位样式 */
.position-list-blank {
  display: inline-block;
  width: 100%;
  padding: 82px;
  text-align: center;
  font-size: 12px;
  color: rgba(0,0,0,0.28);
}

#position-preview-modal.section-content {
  display: none;
  min-height: initial;
  height: auto;

  /* padding: 24px; */
  .list-item-content {
    p {
      display: inline-block;
      padding-right: 8px;
    }
    span {
      margin: 0;
      padding: 0;
    }
  }
  .section-content-main {
    padding: 24px;
  }
}
#company-modal {
  width: 600px;
  min-width: 600px;

  #company-edit {
    .btn {
      margin-left: 16px;
    }
  }

}
#status-modal {
  label {
    padding: 0;
  }
}

.phase {
  height: 100%;
  padding-top: 24px;
  text-align: center;
  &.welcome {
    p {
      padding-top: 12px;
    }
  }

  &.greet {

    .greet-title {
      width: 100%;
      color: rgba(0,0,0,0.82);
      font-size: 16px;
      padding-bottom: 12px;

    }
    span {
      color: $primary-color;
    }
    .caption {
      background: #FAFAFA;
      color: rgba(0,0,0,0.54);
      display: inline-block;
      font-size: 14px;
      line-height: 32px;
      padding: 24px;

      .cption-bottom {
        margin-top: 24px;
      }
    }
    .jx-wx-code {
      padding: 24px;
      color: rgba(0,0,0,0.54);
      font-size: 14px;

      img {
        @include size(113px);
      }
    }

    .greet-footer {
      color: rgba(0,0,0,0.54);
      font-size: 14px;
      padding: 12px;
      display: inline-block;
      text-align: left;
    }
  }
}


#chat-item-input {

  .input-container {
    .chat-input {
      height: 114px;
    }
  }
}
