$tag-arrow-width: 6px;
$tag-font-size: 12px;
$tag-height: 24px;
$tag-font-color: #FFF;
$tag-radius: 2px;
$tag-colors: (
    "accept": "#17B082",
    "refuse": "#F4A53E",
    "success": "#158EC5",
    "failed": "#F24C70",
    "being": "#17B082",
    "close": "#9A9A9A",
    "unfit": "#F24C70",
    "no-fd": "#9A9A9A",
    "wait": "#9A9A9A"
);

.status-tag {
    color: $tag-font-color !important;
    display: inline-block;
    font-size: $tag-font-size;
    height: $tag-height !important;
    line-height: $tag-height !important;
    padding: 0 $tag-font-size;
    position: relative;
    margin: 0;
    margin: 0 6px 0 12px;
    @include border-radius($tag-radius);

    @each $name, $color in $tag-colors {

        &.#{$name} {
        border: 1px solid #{$color};
        background: #{$color};
        &::before {
            border: solid transparent;
            content: ' ';
            position: absolute;
            width: 0px;
        }

        &::before {
            border-width: $tag-arrow-width;
            border-right-color: #{$color};
            left: - $tag-arrow-width * 2 -1;
            top: $tag-height/2 - $tag-arrow-width;
        }
    }
    }
}

