$corner-size: 57px;
$corner-font-size: 14px;

.corner-status-tag {
    min-height: $corner-size;
    min-width: $corner-size;
    .corner-container {
        display: inline-block;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 $corner-size $corner-size 0;
        line-height: 0px;
        _border-color: #000000 #007bff #000000 #000000;
        _filter: progid:DXImageTransform.Microsoft.Chroma(color='#000000');
        -webkit-transform:rotate(360deg);
        /* for a better anti-aliasing in webkit browser */
    }

    @each $name, $color in $tag-colors {
        &.#{$name} {
        .corner-container {
            border-color: transparent  #{$color} transparent transparent;
        }
    }
    }

    span {
        color: #FFF;
        display: inline-block;
        font-size: $corner-font-size;
        font-weight: bold;
        position: absolute;
        transform: rotateZ(45deg);
        top: 11px;
        right: 3px;
        width: 30px;
    }

    &.wait span, &.unfit span{
        font-size: 12px;
        top: 11px;
        right: 1px;
        width: 36px;
    }
    &.success span{
        font-size: 12px;
        top: 4px;
        right: 1px;
        width: 30px;
    }
}