footer {
  @include max-screen(480px) {
    height: $footer-height * 2;
  };
  .footer-content {
    @include max-screen(480px) {
      padding: 12px 24px 24px 36px;
    }
  }
  .footer-item {
    @include max-screen(480px) {
      display: inline-block;
      width: 100%;

      p {
        line-height: 24px;
      }

      &.footer-company-intro {

        .intro-bottom {
          position: absolute;
          bottom: -64px;
        }
      }
      &.footer-company-contact {
        text-align: left;
        margin-top: 3px;
      }
    }
  }

  .intro-top {
    @include max-screen(480px) {
      display: inline-block;
      width: 100%;

      &.mobile {
        display: block;
      }
      &.pc {
        display: none;
      }
    }
    .intro-bottom {
        @include max-screen(480px) {
          display: none;
        }
      }
  }

}
