/* 一些list的样式 */
.details-container {
  padding: 24px 24px;
}

/* 人才管理的list */
/* 职位管理的list */
/* 自定义各自的高度 */
.list-item {

  position: relative;
  display: block;
  vertical-align: top;
  width: 100%;
  /* height: 140px; */
  font-size: 0;
  padding: 16px 24px;
  cursor: pointer;
  @extend .bottom-border;
  background: #FFF;

  a {
      display: inline-block;
  }
  .job-extra {
      display: inline-block;
      position: relative;
      vertical-align: top;
  }
  &:hover {
    background: $list-action-bg-color;
  }
  &.short {
      height: 81px;
  }
  .li-link {
      display: block;
      padding: 0;
      margin: 0;
      width:100%;
  }
  .list-extra {
    font-size: 12px;
    position: absolute;
    right: 24px;
    text-align: right;
    top: 12px;

    .mini-state-btn  {
      margin: 0;
      margin-top: 5px;
    }

    time {
      display: inline-block;
      width: 100%;
      color: rgba(0,0,0,0.28);
      vertical-align: top;
    }

  }
  /* 受上面font-size的影响 */
  p,label,span,li {
    @extend .talent-content-font;
  }

  .item-atom {
    display: inline-block;
    vertical-align: top;
    width: 33.33%;
  }

  .list-action {
    @extend .list-action-font;
    padding-top: 12px;
    position: absolute;
    right: 0;
    top: 0;
    width: 60px;
    height: 100%;
    display: none;
    background: $list-action-bg-color;

    a {
      display: none;
    }
    .list-action-item {
      line-height: 32px;
      text-align: center;

      a {
        display: inline-block;
        color: rgba(0,0,0,0.54);
      }
      &:hover {
        background: $list-action-hover-color;
      }
    }
  }

  &.active,
  &:hover {
    background: $list-active-color;

    .list-action {
      display: block;
    }
  }
}
/* 职位列表，以及职位详情 */
.list-item-content {
  color: rgba(0,0,0,0.87);
  .info-top {
    @extend .common-title-font;
    width: 100%;
    font-weight: 400;
    span {
      margin-right: 16px;
    }
  }

  .info-bottom {
    padding-top: 12px;
    @extend .position-info-font;
    span {
      padding-right: 8px;
    }
    .info-list-item {
      width: 100%;
      color: rgba(0,0,0,0.54);
      display: inline-block;
      padding: 4px 0;

      label {
        color: rgba(0,0,0,0.28);
      }

      p {
        display: inline-block;
      }
    }
  }
}

.pic-container {
  /* 48px */
  @include size($card-pic-container-size);
  display: inline-block;
  vertical-align: top;

  img {
    cursor: pointer;
    max-width: 100%;
    max-height: 100%;
  }
}

.company-general-info {
  display: inline-block;
  padding:0 16px;
  width: 320px;
  height: 100%;

  .info-company-name {
    line-height: 14px;
    padding-left: 8px;
    border-left: 2px solid #13A277;

    label {
      color: rgba(0,0,0,0.87);
    }
  }
}

.invitation-action {
  display: inline-block;
  text-align: right;
  vertical-align: top;
  margin-top: 12px;

  .mini-state-btn {
      margin-right: 0;
  }
}

/* 与resume中的section-item通用 */
.section-item {
  padding: 30px 0;

  .section-item-title {
    padding-bottom: 16px;
    @extend .section-item-title-font;
  }

  .block-content {
    /*         16+24=40px有序列表的数字部分直接左侧完全对齐 */
    /* padding: 12px 0 12px 40px; */
    @extend .section-item-block-font;

    .block-content-item {
      padding: 4px 0;

      p {
        margin-right: 12px;
      }
    }
  }
  .section-block-title {

    label {
      display: inline-block;
      padding-left: 12px;
      border-left: 3px solid black;
      @extend .section-item-title-font;
    }
  }
}


.jx-list {
  position: relative;

  .jx-list-item {
    display: inline-block;
    width: 100%;
    height: 32px;
    line-height: 32px;
    cursor: pointer;
    position: relative;
    padding-left: 12px;
    @include border-radius(2px);

    .flag {
      display: none;
      position: absolute;
      top: 12px;
      right: 0;
    }

    &.active,
    &:hover {
        background: #E6E6E6;
      /* @extend .bottom-border; */

      .flag {
        display: block;
      }
    }
  }

}
