/* 在个人页面出现的职位样式 */
.job-info {
  /* padding:0 16px; */
  display: inline-block;
  width: 300px;
  /* width: 100%; */

  &.long {
    width: 100%;
  }

  label {
    display: inline-block;
    @extend .job-time-font;
  }
}

/* 在HR页面出现的职位样式 */
.job-info-long {
  display: inline-block;
  width: 480px;

  label {
    display: inline-block;
    @extend .job-time-font;
  }

}
/* 保证其他的地方的title一样 */
/* TODO 提取出来 */
.job-info-hit {

  span.job-name,
  span#job-name{
    color: #4BB08E;
    display: inline-block;
    max-width: 200px;
    font-size: 14px;
    @extend .text-ellipsis;
  }
  .job-salary,
  #job-salary{
    display: inline-block;
    font-weight: bold;
    font-size: 16px;
  }
}

.job-extra.long {
  width: 200px;
  /* time, a {
     display: inline-block;
     } */
}
.job-extra {
  text-align: right;
  width: 155px;
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;

  time {
    display: inline-block;
    /* position: absolute; */
    /* bottom: 0;
       left: 0; */
    width: 100%;
    @extend .job-time-font;
  }

  label {
    display: inline-block;
    height: 24px;
    padding-top: 16px;
  }
  .invitation-num {
    margin: 0;
  }
}
/* 带有详细信息的header */
.details-header {
  position: relative;
  @extend .bottom-border;
  /* padding: 24px 16px 16px 24px; */
  padding-bottom: 16px;

  #hire-size {
    padding: 0;
  }

  .list-item-content {

    .info-item {
      display: inline-block;
      vertical-align: top;
      padding-right: 12px;

      .item-title, .item-content {
        font-size: 12px !important;
        color: rgba(0,0,0,0.54) !important;
      }
    }
    .info-bottom {
      p, span {
        color: rgba(0,0,0,0.87);
      }
    }
  }

  .header-extra {
    position: absolute;
    top: 0;
    right: 0;
    text-align: right;

    .dist-time {
      font-size: 12px;
      color: rgba(0,0,0,0.28);
    }

    .extra-action {
      margin-top: 12px;

      .list-action-item {
        display: inline-block;
        cursor: pointer;
        color: $primary-color;


        &[data-type] {
          color: red;
        }
      }
    }
  }
}
