$weixin-sidebar-width: 110px;
$weixin-sidebar-height: 172px;
                           
.main-container {

    .extra-sidebar {
        @extend .z-depth-1;
        position: absolute;
        top: $header-height +12px;
        @include size($weixin-sidebar-width, $weixin-sidebar-height);
        right: - ( $weixin-sidebar-width + 12px); 
    }
    .weixin-code {
        background-color: #fff; 

        .weixin-cross {
            color: rgba(0,0,0,0.54);
            cursor: pointer; 
            font-size: 16px;
            height: 28px;
            text-align: right; 
            padding-top: 4px;
            padding-right: 8px;
            width: 100%;
        }

        img {
            @include size(88px);
        }

        .weixin-caption {
            color: rgba(0,0,0,0.54);
            font-size: 12px;
            padding-top: 12px;
            text-align: center;
        }
    }
}