.weixin-modal {
  /* display: none; */
    background-color: transparent;
  position: absolute;
  min-height: 538px;
  height: 538px;
  top: 56px;
  overflow: hidden;

  .modal-bg {
    display: inline-block;
    height: 538px;
  }

  .modal-close {
    @include size(36px);
    @include border-radius(18px);
    background: #FFF;
    line-height: 36px;
    position: absolute;
    text-align: center;
    top: 4px;
    right: 4px;
    cursor: pointer;
    span {
      font-size: 24px;
      color: rgba(0,0,0,0.54);
    }
  }
  .weixin-code-content {
      position: absolute;
      top: 160px;
      left: 250px;
      img {
          @include size(100px);
      }
      
      .weixin-modal-caption {
          font-size: 16px; 
      }
  }
}